<template>   
 <div id="container"></div>

</template>
<script>
import osc from "osc";
//import * as Three from 'three';

var port = new osc.WebSocketPort({
          url: "wss://circusfamilyprojects.nl:8084" //  ws://localhost:8083 online server wss://circusfamilyprojects.nl:8084
        }); 

 port.open();

export default {
   
  data() {
  return{
      camera: null,
      scene: null,
      renderer: null,
      mesh: null,
      groundMesh: null,
      meshPilaar: null,
      light: null,
      //Three

   }
  },
  computed: {
  
  },
  created() {
    
    },
  mounted(){
   // this.Three = window.Three = require('Three');
    this.init();
    this.animate();
  
    },
  methods:{
    

    init: function() {
       
        let container = document.getElementById('container');
         // renderer
         this.renderer = new this.$three.WebGLRenderer({antialias: true});
        this.renderer.shadowMap.enabled = true;
        this.renderer.shadowMap.type = this.$three.PCFShadowMap;

        this.renderer.setSize(container.clientWidth, container.clientHeight);
        container.appendChild(this.renderer.domElement);

        //camera
        this.camera = new this.$three.PerspectiveCamera(100, container.clientWidth/container.clientHeight, 0.1, 100);
        this.camera.position.z = 10;

        //scene
        this.scene = new this.$three.Scene();
        

         var ground = new this.$three.BoxGeometry( 20,20, 0 );
         var ColorGeometry = new this.$three.Geometry();
         var PilaarGeometry = new this.$three.Geometry();

    

         //vlak bovenkant kleur
        ColorGeometry.vertices.push(new this.$three.Vector3(0, 0,  0.0));  // x,y,z  0  // 0 is de hoogte
        ColorGeometry.vertices.push(new this.$three.Vector3(4, 4,  0.0));  // x,y,z  1
        ColorGeometry.vertices.push(new this.$three.Vector3(-4, 4,  0.0));  // x,y,z  2
        ColorGeometry.vertices.push(new this.$three.Vector3(4, -4,  0.0));  // x,y,z  3
        ColorGeometry.vertices.push(new this.$three.Vector3(-4, -4,  0.0));  // x,y,z  4
        
        //  geometry.vertices.push(new this.$three.Vector3(2, 2,  9));  // x,y,z  5  // 6 is de hoogte
        // geometry.vertices.push(new this.$three.Vector3(-2, 2,  9));  // x,y,z  6
          //  geometry.vertices.push(new this.$three.Vector3(2, -2,  9));  // x,y,z  7
            // geometry.vertices.push(new this.$three.Vector3(-2, -2, 9));  // x,y,z  8
        
             //vlak bovenkant paal outline
        PilaarGeometry.vertices.push(new this.$three.Vector3(0, 0,  1));  // x,y,z  0  // 0 is de hoogte
        PilaarGeometry.vertices.push(new this.$three.Vector3(2, 2,  1));  // x,y,z  1
        PilaarGeometry.vertices.push(new this.$three.Vector3(-2, 2,  1));  // x,y,z  2
        PilaarGeometry.vertices.push(new this.$three.Vector3(2, -2,  1));  // x,y,z  3
        PilaarGeometry.vertices.push(new this.$three.Vector3(-2, -2,  1));  // x,y,z  4

        ColorGeometry.faces.push(
        // top
        new this.$three.Face3(0, 1, 2), // vlak 1
        new this.$three.Face3(0, 3, 1), // vlak 2
        new this.$three.Face3(0, 4, 3), // vlak 3
        new this.$three.Face3(0, 2, 4), //vlak 4

        // new this.$three.Face3(2, 1, 5), // zijkant vlak 1
        // new this.$three.Face3(2, 5, 6), // zijkant vlak 1
        // new this.$three.Face3(1, 7, 5), // zijkant vlak 2
        // new this.$three.Face3(1, 3, 7), // zijkant vlak 2
        // new this.$three.Face3(3, 8, 7), // zijkant vlak 3
       //  new this.$three.Face3(3, 4, 8), // zijkant vlak 3
        // new this.$three.Face3(4, 6, 8), // zijkant vlak 4
       //  new this.$three.Face3(4, 2, 6), // zijkant vlak 4
              )

              // top outline
            PilaarGeometry.faces.push(
              // top
              new this.$three.Face3(0, 1, 2), // vlak 1
              new this.$three.Face3(0, 3, 1), // vlak 2
              new this.$three.Face3(0, 4, 3), // vlak 3
              new this.$three.Face3(0, 2, 4), //vlak 4

              )
          
          var faces = ColorGeometry.faces;
            for ( var face of faces ) {
              face.color.set( Math.random() * 0xffffff ); //color faces with random color
            }

           ColorGeometry.colorsNeedUpdate = true;

             ColorGeometry.computeFaceNormals();
             ColorGeometry.computeVertexNormals();

            // pilaar kleur 
          var material = new this.$three.MeshBasicMaterial({ vertexColors: this.$three.FaceColors, side: this.$three.DoubleSide});  //vertexColors: this.$three.FaceColors, side: this.$three.DoubleSide 
          this.mesh = new this.$three.Mesh( ColorGeometry, material );

          //this.mesh.rotation.x += -0.8
          //this.mesh.rotation.y += 3.1

           // pilaar outline
          var materialPilaar = new this.$three.MeshBasicMaterial({ color: '#FFFFFF',    wireframe: true, linewidth: 4 });  //vertexColors: this.$three.FaceColors, side: this.$three.DoubleSide 
          this.meshPilaar = new this.$three.Mesh( PilaarGeometry, materialPilaar  );

           // grond
          var groundMaterial = new  this.$three.MeshBasicMaterial( {color: '#484a4a'} );
          this.groundMesh = new this.$three.Mesh( ground, groundMaterial );
          this.groundMesh.position.set(0, 0, -1)
          //this.groundMesh.rotation.x += -0.2
          //this.groundMesh.rotation.y += 3.1
         

          // light
          this.light = new this.$three.DirectionalLight(0xffffff,1.5,10); // soft white light
          this.light.position.set( 0, 20, 40 );
          this.light.rotation.x += -1
           this.light.rotation.y += -2
          this.light.castShadow = true;
          
           

          this.scene.add(this.mesh,this.groundMesh,this.light, this.meshPilaar );
    },
    animate: function() {
        requestAnimationFrame(this.animate);
        
       //this.mesh.rotation.x += 0.01;    
      //this.mesh.rotation.y += 0.02;
        this.renderer.render(this.scene, this.camera);
    }


    }
}
</script>

<style lang="scss" scoped>

#container {
  
  height: 100%;
  width: 100%;

}




 

</style>